/*
*     
*/
export default class DestinationsModel {
  #destinations = null;
  #service = null;

  /**
   * @param {object}      
   * @param {Array}         
   **/
  constructor(service) {
    this.#service = service;
    this.#destinations = [...this.#service.getDestinations()];
  }

  /**
   *       
   * @returns {Array}   
   */

  get destinations () {
    return this.#destinations;
  }

  /**
   *       
   * @param {string} id   
   * @returns {object}    
   */

  getById(id) {
    return this.#destinations.find((destination) => destination.id === id);
  }
}
