/**
 *      
 */

export default class OffersModel {
  #service = null;
  #offers = null;

  /**
   *       
   * @param {object} service      
   * @param {Array}         
   */

  constructor(service) {
    this.#service = service;
    this.#offers = [...this.#service.getOffers()];
  }
  /**
   *     
   * @returns     
   */

  get offers() {
    return this.#offers;
  }

  /**
   *       
   * @param {string} type       
   * @returns    
   */

  getByType(type) {
    return this.#offers.find((offer) => offer.type === type);
  }

  /**
   *             
   * @param {object} offersTripPoint    
   * @returns    
   */

  getById(offersTripPoint) {
    if (offersTripPoint) {


      const
        typeOffer = offersTripPoint.type,
        offersTripPointIds = offersTripPoint.offers,
        offersOfType = this.getByType(typeOffer);

      return offersTripPointIds
        .map((id) => offersOfType.offers
          .find((offer) => offer.id === id)
        );
    }
    return [];

  }

}
