
/**
 *      
 */

export default class TripPointsModel {
  #service = null;
  #tripPoints = null;

  /**
   *       
   * @param {object} service     
   * @param {Array}          
   */

  constructor(service) {
    this.#service = service;
    this.#tripPoints = [...this.#service.getTripPoints()];
  }

  /**
   *     
   * @returns   
   */

  get tripPoints() {
    return this.#tripPoints;
  }
}
